<template>
  <v-container fluid>
    <p style="margin-top: 2%;" class="black--text text-center">
      Gib bitte zuerst Deinen Assistant Code ein!
    </p>

    <v-form
      ref="form"
      v-model="valid"
      lazy-validation
    >

    <v-text-field
      :rules="assistant_rules"
      label="Assistant Code"
      class="centered-input mt-6 pt-0 mb-4"
      light
      required
      v-model="globalData.Test.Bearbeiter"
    ></v-text-field>

    </v-form>

    <p class="black--text text-center">
      Im nächsten Schritt wird eine Testperson erfasst.
    </p>

    <div class="u text-center">
      <v-alert
        class="text-center white--text"
        style="width: 33%; margin-left: 33.333%;"
        dense
        color="primary"
        
        >Basic</v-alert
      >

      <br />
      <p>
        Du kannst jederzeit die Daten auf Deinem Smartphone/Tablet speichern -
        und später, wenn Du Internetverbindung hast, uploaden.
      </p>

      <v-btn
      :disabled="!valid"
        @click="toUser()"
        style="width: 80%; margin-top: 5%;"
        ma-5
        color="secondary"
        class="white--text"
        >Testperson anlegen</v-btn
      >
    </div>

      <div class="text-center">
      <v-dialog class="mx-auto" v-model="dialog" width="500">
        <v-card
          class="text-center"
        >
          <v-card-text></v-card-text>
          <p
            style="font-family: calibri; font-size: 110%;"
            class="primary--text"
          >
            Zum Fortfahren bitte den Assistant Code eingeben!
          </p>

          <v-container class="text-xs-center">
            <v-card-actions id="act">
              <v-layout row>
                <v-flex justify-center>
                  <v-btn
                  style="width: 50%;"
                    dark
                    color="primary"
                    @click="dialog = false"
                  > Ok
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>

  </v-container>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Home',
  props: {
    globalData: {
      type: Object,
    },
  },
  components: {},

  //   validations: {
  //   assistant_code: { required, maxLength: maxLength(10) },
  // },

  data() {
    return {
      dialog: false,
      valid: true,
      assistant_rules: [
        value => value.length <= 21 || 'Der Assistant Code hat 21 Zeichen.',
        value => !!value || 'Bitte Code eingeben.',
      ]
    };
  },

  methods: {
    toUser() {
      if (this.globalData.Test.Bearbeiter != "") {
      this.$router.push({ name: 'Userinfo' });
      } else {
        this.dialog = true;
      }
    },
  },
};
</script>


<style lang="css" scoped>
.centered-input >>> input {
  text-align: center;
  color: black;
  /* background-color: #bdbdbd; */
  font-size: 1.2em;
}

.u {
  margin-top: 10%;
}

.v-overflow-btn .v-input__slot::before {
  border-color: blue !important;
}

/* #quad {
  color: #1f497d;
  padding-top: 60px;
  border: 3px #1f497d solid;
  margin-left: 27%;
  width: 150px;
  height: 150px;
} */
</style>


<style lang="scss">
.centered-input.v-text-field .v-label {
  font-size: 1.2em;
  margin-bottom: 78.5%;
  left: 50% !important;
  transform: translateX(-50%);
  transform-origin: top 50%;
  &.v-label--active {
    transform: translateY(-18px) scale(.75) translateX(-50%);
  }
}
</style>

