<template>
  <v-app style="background: white" dark>
    <v-app-bar app color="white" elevation="2" v-if="$route.name != 'Login'">
      <v-container fluid>
        <v-layout justify-center align-center>
          <img
            @click="$router.push({ name: 'Login' })"
            class="ma-2 pa-n4"
            src="./../public/img/Logo/Logo-dmt.jpg"
          />

          <v-btn
            v-if="$route.name == 'Userinfo'"
            id="backtohome"
            color="white"
            class="primary--text"
            fab
            depressed
            rounded
            @click="backtohome()"
          >
            <v-icon color="primary">mdi-home</v-icon>
          </v-btn>

          <v-btn
            v-if="$route.name == 'Testform'"
            style="position: absolute; top: 0; right: 0"
            id="logout"
            color="white"
            class="primary--text"
            fab
            depressed
            @click="showTable()"
          >
            <v-icon>mdi-table</v-icon>
          </v-btn>
        </v-layout>
      </v-container>

      <v-spacer></v-spacer>
    </v-app-bar>

    <v-main>
      <router-view
        fixed
        @userLogin="userLogin()"
        :globalData="globalData"
        @googleUserData="data => (globalData.googleUser = data)"
        @testCreated="testCreated"
      ></router-view>
      <div class="text-center">
        <v-dialog persistent v-model="isInstallable" width="500">
          <v-card class="text-center">
            <v-card-text></v-card-text>
            <span class="primary--text">
              Möchtest du die DMT App installieren?
            </span>

            <v-container class="text-xs-center">
              <v-card-actions id="act">
                <v-layout row>
                  <v-flex justify-center>
                    <v-btn dark color="primary" @click="installPWA">
                      <v-icon dark color="white">mdi-check</v-icon>
                    </v-btn>

                    <v-btn
                      style="margin-left: 30%;"
                      dark
                      color="primary"
                      @click="isInstallable = false"
                    >
                      <v-icon dark color="white">mdi-close</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </v-card-actions>
            </v-container>
          </v-card>
        </v-dialog>
      </div>
    </v-main>

    <v-snackbar v-model="snackbar" timeout="5000" centered color="red">
    <v-icon>
      mdi-alert
    </v-icon>
      A problem occured during data transmission, see the console for details...
    </v-snackbar>

    <!--  Navbar unten -->
    <v-footer
      color="white"
      elevation="2"
      fixed
      padless
      v-if="
        $route.name == 'Home' ||
          $route.name == 'Userinfo' ||
          $route.name == 'Login'
      "
    >
      <v-container fluid class="text-center">
        <h5 v-if="$route.name == 'Login'" class="text-center black--text">
          Powered by mb-mediasports ©
        </h5>
        <h5 v-if="$route.name != 'Login'" class="text-center black--text">
          Motor Health Promotion ©
        </h5>
      </v-container>
    </v-footer>
  </v-app>
</template>

<script>
import axios from 'axios';

export default {
  name: 'App',
  components: {},
  data() {
    return {
      globalData: {
        slide: 0,
        
        Test: {
          Bearbeiter: 'E-C03F-6B8C-CJ16-T7KD',
          Testdatum: '2020-09-19T00:00:00',
          // GeocodeN: '48.863517',
          // GeocodeE: '8.673314',
          Gruppen: [
            {
              Gruppe: '001',
              Probanden: [
                {
                  ProbandenNr: '21',
                  Identifier: 'E-C03F-6B8C-CJ16-T7KD-021',
                  PCode: "P-KK7F-HNMD-LK3X-PNMY",
                  Vorname: 'Lukas', //mandatory
                  Nachname: 'Hechenberger', //mandatory
                  Geschlecht: 'm', //mandatory
                  Geburtsdatum: '2000-10-04T00:00:00', //mandatory
                  Groesse: 178.0,
                  Gewicht: 60.0,
                  Sitzhoehe: 54.0,
                  Bemerkung: null,
                  ErhebungAbgelehnt: true,
                  ErhebungKinderturnen: false,
                  ErhebungVereinsmitglied: false,
                  Test: {
                    Sprint_D1: 4.67,
                    Sprint_D2: 3.99,
                    Bal_D1_6: 8,
                    Bal_D1_4_5: 8,
                    Bal_D1_3: 8,
                    Bal_D2_6: 8,
                    Bal_D2_4_5: 8,
                    Bal_D2_3: 8,
                    SHH_D1: 12.0,
                    SHH_D2: 13.0,
                    RB_D1: 10.0,
                    RB_D2: 10.0,
                    LS_Anzahl: 20.0,
                    SU_Anzahl: 30.0,
                    SW_D1: 120.0,
                    SW_D2: 110.0,
                    Ausdauer_Rundenzahl: 1,
                    Ausdauer_Reststrecke: 2.0,
                  },
                },
              ],
            },
          ],
        },
      },
      isInstallable: false,
      deferredPrompt: null,
      snackbar: false,
    };
  },
  created() {
    window.addEventListener('beforeinstallprompt', e => {
      // Prevent the mini-infobar from appearing on mobile
      console.log('install fired!');
      e.preventDefault();
      // Stash the event so it can be triggered later.
      this.deferredPrompt = e;
      // Update UI notify the user they can install the PWA
      this.isInstallable = true;
    });
    window.addEventListener('appinstalled', () => {
      // Log install to analytics
      console.log('INSTALL: Success');
    });
  },
  methods: {
    installPWA() {
      this.deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      this.deferredPrompt.userChoice.then(choiceResult => {
        if (choiceResult.outcome === 'accepted') {
          console.log('User accepted the install prompt');
        } else {
          console.log('User dismissed the install prompt');
        }
        this.deferredPrompt = null;
        this.isInstallable = false;
      });
    },
    backtohome() {
      this.$router.push({ name: 'Home' });
    },
    showTable() {
      this.globalData.slide = 8;
    },
    closeTable() {
      this.$router.push({ name: 'Testform' });
    },
    async testCreated() {
      try {
        console.log(JSON.stringify(this.globalData.Test));
        let res = await axios({
          method: 'post',
          url: 'https://dmt.motoriktest.eu/api/v1/ProbandTest',
          data: this.globalData.Test,
          headers: {
            'Content-type': 'application/json',
          },
        });
        console.log(res.data);
        this.globalData.PCode = res.data[0].Gruppen[0].Probanden[0].PCode;
      } catch (error) {
        this.snackbar = true;
        console.log(error);
      }
      
    },
    showNetworkAlert() {

      window.setInterval(() => {
        this.alert = false;
      }, 5000)    
    }
  },
};
</script>

<style>
.v-bottom-navigation--fixed {
  position: fixed !important;
}

.v-item-group.v-bottom-navigation .v-btn {
  height: inherit !important;
}

img {
  margin-top: -0.5rem;
  max-height: 2.5rem;
}

/* #Nav {
  border-top: #ff6600 1.5px solid !important;
} */

#backtohome {
  position: absolute;
  top: 0;
  left: 0;
}
</style>
