<template>
  <v-container fluid>
        <v-text-field
      label="Assistant Code"
      class="centered-input mt-4 mb-2 pt-0 mb-4"
      light
      readonly
      required
      v-model="globalData.Test.Bearbeiter"
    ></v-text-field>

    <v-form
      ref="form"
      v-model="valid"
      lazy-validation
    >

    <v-text-field
      label="Laibchen-Nr"
      placeholder="Laibchen-Nr"
      v-model="globalData.Test.Gruppen[0].Probanden[0].ProbandenNr"
      class="centered-input mt-8 pt-0"
      style="width: 90%; margin-left: 5%"
      :rules="laibchen_rules"
    ></v-text-field>

    <v-container style="width: 90%; margin-left: 5%" fluid>
      <v-row>
        <v-text-field
          label="Vorname"
          placeholder="Vorname"
          v-model="globalData.Test.Gruppen[0].Probanden[0].Vorname"
          class="centered-input mt-4 pt-0"
          :rules="vorname_rules"
        ></v-text-field>
      </v-row>

      <v-row>
        <v-text-field
          label="Nachname"
          placeholder="Nachname"
          v-model="globalData.Test.Gruppen[0].Probanden[0].Nachname"
          class="centered-input mt-4 pt-0"
          :rules="nachname_rules"
        ></v-text-field>
      </v-row>

      <v-row>
        <div class="text-center">
          <v-radio-group
            class="text-center"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Geschlecht"
            row
          >
            <v-radio label="weiblich" value="w"></v-radio>
            <v-radio label="männlich" value="w"></v-radio>
            <v-radio label="divers" value="d"></v-radio>
          </v-radio-group>
        </div>
      </v-row>

      <v-row>
        <v-col cols="6" sm="6">
          <v-text-field
            label="Größe"
            type="number"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Groesse"
            class="centered-input pt-0"
            :rules="groeße_rules"
            suffix="cm"
          ></v-text-field>
        </v-col>

        <v-col cols="6" sm="6">
          <v-text-field
            label="Gewicht"
            type="number"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Gewicht"
            class="centered-input pt-0"
            :rules="gewicht_rules"
            suffix="kg"
          ></v-text-field>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6" sm="6">
          <v-text-field
            label="Geburtsdatum"
            placeholder="dd/mm/yyyy"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Geburtsdatum"
            class="centered-input pt-0"
            :rules="birth_date_rules"
          ></v-text-field>
          <!-- <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="490px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  class="centered-input mb-4"
                  v-model="date_birth"
                  label="Geburtstag"
                  prepend-inner-icon="mdi-calendar-month"
                  readonly
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                ref="picker"
                v-model="date_birth"
                :max="new Date().toISOString().substr(0, 10)"
                min="1998-01-01"
              ></v-date-picker>
            </v-menu>-->
        </v-col>

        <v-col cols="6" sm="6">
          <v-text-field
            label="Sitzhöhe"
            type="number"
            suffix="cm"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Sitzhoehe"
            class="centered-input pt-0"
            :rules="sitzhoehe_rules"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-textarea
            outlined
            id="freitext"
            label="Bemerkung"
            placeholder="Freitext"
            v-model="globalData.Test.Gruppen[0].Probanden[0].Bemerkung"
            class="centereed-input pt-0"
            maxlength="50"
          ></v-textarea>
        </v-col>
      </v-row>


      <v-btn
      :disabled="!valid"
        @click="start()"
        color="secondary"
        style="width: 80%; margin-left: 10%; margin-bottom: 7%"
        class="white--text"
        >Test starten</v-btn
      >


      <p></p>
    </v-container>
      </v-form>

  </v-container>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Userinfo",
  props: {
    globalData: {
      type: Object,
    },
  },
  components: {},

  data() {
    return {
      valid: true,
      gender: null,
      date_birth: null,
      menu: null,
      laibchen_rules: [
        (v) => (v >= 1 && v <= 1000) || "Laibchennummer abgelehnt (1-999)",
      ],
      gender_rules: [
        (v) =>
          v == "weiblich" ||
          v == "männlich" ||
          v == "divers" ||
          "Gender nicht angenommen (w/m/d)",
      ],
      vorname_rules: [
        v => !!v || 'Vorname eingeben',
        v => v.length <= 30 || 'Vorname zu lang (1-30 Zeichen)'
        ],

      nachname_rules: [
        v => !!v || 'Nachname eingeben',
        v => v.length <= 30 || 'Nachname zu lang (1-30 Zeichen)'
        ],

      gewicht_rules: [
        v => !!v || 'Gewicht eingeben',
        (v) => (v >= 30 && v <= 300) || "Gewicht nicht angenommen (30kg-300kg)",
        ],

      groeße_rules: [
        v => !!v || 'Größe eingeben',
        (v) => (v >= 50 && v <= 250) || "Größe nicht angenommen (50cm - 250cm)"
        ],

      birth_date_rules: [
        (v) =>
          v >= "01/01/2000" ||
          v <= "01/01/2014" ||
          "Geburtsdatum nicht angenommen",
      ],
   
      sitzhoehe_rules: [
        (v) => (v >= 25 && v <= 55 || v == 0) || "Sitzhöhe nicht angenommen (25cm-55cm)",
      ],
    };
  },
  methods: {
    start() {
        this.$router.push({ name: "Testform" });
    },
  },
};
</script>

<style scoped>
.centered-input >>> input {
  text-align: center;
  color: black;
  /* background-color: #bdbdbd; */
  font-size: 1.2em;
}

#freitext {
  font-size: 1.2em;
}
</style>
